import React, { Component } from 'react';
import { BrowserRouter, Route, Link, Switch } from 'react-router-dom';
import logo from './logo.svg';
import './App.css';


function Home() {
  return <h1>Halman Home</h1>
}


function ListView() {
  return (
    <div>
  <h1>Halman Users</h1>
  <ul>
    <Link to='users/rii'>Rii</Link><br />
    <Link to='users/Hill'>Hill</Link>

  </ul>
  </div>
  )
}

function DetailView({ match}) {
  return <h1>ini halaman {match.params.name}</h1>
}


function NotMatch() {
  return <h1>gada</h1>
}

class App extends Component {
 

  render() {
   
    return (
      <BrowserRouter>
      <div>
        <nav>
          <li><Link to="/"> Home </Link></li>
          <li><Link to="/users"> Iser </Link></li>

        </nav>

        <main>
          <Switch>
            <Route path="/" exact component={Home}></Route>
            <Route path="/users" exact component={ListView}></Route>
            <Route path='/users/:name' exact component={DetailView}></Route>


            <Route component={NotMatch}></Route>
            </Switch>
        </main>
      </div>
      </BrowserRouter>
    )
  }
}

export default App;
